import React, { useEffect } from "react";
import { NavLink } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { TOKEN_KEY ,ACCOUTN_TYPE} from "../../../../services/Authentication/Types";
import M from "materialize-css";
import $ from "jquery";

export default () => {
  useEffect(() => {
    // var elems = document.querySelectorAll(".sidenav");
    // M.Sidenav.init(elems);

    $("li#logouts").click(function () {
      localStorage.removeItem(TOKEN_KEY);
      localStorage.removeItem(ACCOUTN_TYPE);
      setTimeout(() => {
        window.location.pathname = "/signin";
      }, 1200);
    });
    $(".sidebar-collapse").click(()=>{
      setTimeout(() => {
        $(".back_pack").fadeIn();
      } , 200)
    })

    $(".back_pack").click(()=>{
      $(".back_pack").fadeOut();
      $("ul.side-nav.leftside-navigation").css('transform' , 'translateX(-100%) !important');
    })
  });

  const { t } = useTranslation();
  return (
    <aside id="left-sidebar-nav">
      <div className="back_pack"></div>
      <ul id="slide-out" className="side-nav fixed leftside-navigation">
        <li className="user-details cyan darken-2">
          <div className="row">
            <div className="col col s4 m4 l4">
              <img
                src="/img/avatar/avatar-13.png"
                alt=""
                className="circle responsive-img valign profile-image cyan"
              />
            </div>
            <div className="col col s8 m8 l8">
              <p className="user-roal" id="user-roel-det">
                Emran rastadi sdvsdvvvvvvvvvvvvvvvvvvv
              </p>
              <p className="user-roal">doctor</p>
            </div>
          </div>
        </li>
        <li className="no-padding">
          <ul className="collapsible" data-collapsible="accordion">
            <li className="bold">
              <a className="waves-effect waves-cyan">
                <i className="material-icons">pie_chart_outlined</i>
                <NavLink to="/doctor">
                  <span className="nav-text" data-localize="menu.dashboard">
                    {t("Dashboard.l")}
                  </span>
                </NavLink>
              </a>
            </li>

            <li className="bold">
              <a className="waves-effect waves-cyan">
                <i className="material-icons">my_location</i>
                <NavLink to="/add-clinic">
                  <span className="nav-text" data-localize="menu.dashboard">
                    Clinic Address
                  </span>
                </NavLink>
              </a>
            </li>

            <li className="bold">
              <a className="waves-effect waves-cyan">
                <i>
                  <img src="/img/icons/schedule.png" />
                </i>
                <NavLink to="/schedule-list">
                  <span className="nav-text" data-localize="menu.doctors">
                    Schedule
                  </span>
                </NavLink>
              </a>
            </li>

            {/*<li className="bold">*/}
            {/*    <a className="waves-effect waves-cyan">*/}
            {/*        <NavLink to="/questions">*/}
            {/*            <i className="material-icons">help_outline</i>*/}
            {/*            <span className="nav-text" data-localize="menu.question">{t('Questions.l')}</span>*/}
            {/*        </NavLink></a>*/}
            {/*</li>*/}
            {/*<li className="bold">*/}
            {/*    <a className="waves-effect waves-cyan">*/}
            {/*        <NavLink to="/">*/}
            {/*            <i><img src="/img/icons/ticket.png"/></i>*/}
            {/*            <span className="nav-text">{t('Ticket.l')}</span>*/}
            {/*        </NavLink>*/}
            {/*    </a>*/}
            {/*</li>*/}

            <li className="bold">
              <a className="waves-effect waves-cyan">
                <i>
                  <img src="/img/icons/admin.png" />
                </i>
                <NavLink to="/patient-profile">
                  <span className="nav-text">{t("Patient.l")}</span>
                </NavLink>
              </a>
            </li>

            <li className="bold">
              <a className="waves-effect waves-cyan">
                <i className={"material-icons"}>style</i>
                <NavLink to="/doctor-list-package">
                  <span className="nav-text" data-localize="menu.doctors">
                    {t("package.l")}
                  </span>
                </NavLink>
              </a>
            </li>

            <li className="bold">
              <a className="waves-effect waves-cyan">
                <i>
                  <img src="/img/icons/help.png" />
                </i>
                <NavLink to="/assistant-request">
                  <span className="nav-text">Assisstant</span>
                </NavLink>
              </a>
            </li>
            <li className="bold">
              <a className="waves-effect waves-cyan">
                <i className="material-icons">report</i>
                <NavLink to="/">
                  <span className="nav-text">{t("Report.l")}</span>
                </NavLink>
              </a>
            </li>

            <li className="bold" id="logouts">
              <a className="waves-effect waves-cyan">
                <i className="material-icons">keyboard_tab</i>

                <span className="nav-text">{t("Logout.l")}</span>

              </a>
            </li>
          </ul>
        </li>
      </ul>
      <a
        href="#"
        data-activates="slide-out"
        className="sidebar-collapse btn-floating btn-medium waves-effect waves-light hide-on-large-only"
      >
        <i className="material-icons">menu</i>
      </a>
    </aside>
  );
};
