export const Patient_List_All = () => {
    return async (dispatch) => {
        await Requests.GetAllPatients().then((response) => {
            dispatch({
                type: "GET_ALL_PATIENT_SUCCESS",
                response: response,
            });
        }, error => {
            M.toast({html: "" + error.response.data.error})
        }).catch(error => {
            M.toast({html: 'Network has error! try again ...'})
        })
    };
};


