import {request as Request} from "../../../services/Connection/Request";

export const request = {
    GetAllPatients : () => Request.get("/doctors/patients"),
    GetDetailMe : () => Request.get('/auth/me'),
    GetSchedule : () => Request.get('/schedules'),
    Get_Assistant_Req : () => Request.get('/doctors/assistants'),
    ConfirmAssis : (id) => Request.post('/doctors/assistants/confirm' , id),
    RejectAssis : (id) => Request.delete('/doctors/assistants/reject/'+id),
    ConfirmPat : (id) => Request.post('/doctors/patients/confirm' , id),
    FindPatient : (id) => Request.get('/patients/'+id),
    RejectPatient : (id) => Request.post('/doctors/patients/reject' , id),
    Uploaded: (data) => Request.post("/auth/upload", data),
    GetDoctorClinic : () => Request.get('/doctors/clinics'),
    GetAllClinicForDoctor : () => Request.get("/doctors/clinics"),
    GetOneClinic : (id) => Request.get("/doctors/clinics/"+id),
    DeleteClinicDoctor : (id) => Request.delete("/doctors/clinics/"+id),
    UpdateClinicAddress : (id,data) => Request.put("doctors/clinics/"+id , data),
    AddSchedule : (data) => Request.post("schedules" , data),
    AddNewClinic : (data) => Request.post("/doctors/clinics" , data),
}