import {request as Request} from "../../../services/Connection/Request";
import {path} from "./Types";


export const request = {
    get_admin_dashboard_data : () => Request.get(path.get_dashboard_path),
    GetAllDoctors : () => Request.get("admin/doctors"),
    GetAllPatient : () => Request.get("admin/patients"),
    GetAllClinic : () => Request.get("admin/doctors/clinics"),
    GetAllUsers : () => Request.get("users"),
    GetOneDoctor : (id) => Request.get("admin/doctors/"+id),
    GetOnePatient : (id) => Request.get("admin/patients/"+id),
    GetOneClinic : (id) => Request.get("/admin/doctors/clinics/"+id),
    DeleteOneDoctor : (id) => Request.delete("admin/doctors/"+id),
    DeleteOnePatient : (id) => Request.delete("admin/patients/"+id),
    DelteClinic : (id) => Request.delete("/doctors/clinics/"+id),
    UpdateDoctor : (id ,data) => Request.put("admin/doctors/"+id , data),
    AddSpecialty : (data) => Request.post("admin/specialties" , data),
    GetAllSpecials : () => Request.get("admin/specialties"),
    DeleteSpecilty : (id) => Request.delete("admin/specialties/"+id),
    AddPackage : (data) => Request.post("admin/packages" , data),
    AddPackageDoctor : (data) => Request.post("doctors/packages" , data),
    DeletePackage : (data) => Request.delete("admin/packages/"+data),
    DeleteDoctorPackage : (data) => Request.delete("doctors/packages/"+data),
    GetAllPackage : () => Request.get("admin/packages"),
    GetAllDoctorPackage : () => Request.get("doctors/packages"),
    AddNewUser : (data) => Request.post("superadmin/admins" , data),
    GetAllUserAdmin : () => Request.get("superadmin/admins"),
    SignIn : (data) => Request.post("auth/login" , data),
    SaveTest : (data) => Request.post("admin/tests" , data),
    // uploaded file
    Uploaded: (data) => Request.post("/auth/upload", data),
    //update patient
    UpdatePatient: (id , data) => Request.put("/admin/patients/"+id, data),
    // delete admin by super admin by id
    DeleteAdmin: (data) => Request.delete("/superadmin/admins/"+data),

}
